"use client";
import React, { use, useEffect, useState } from "react";
import { TbCircleDashed } from "react-icons/tb";
import { IoPeople } from "react-icons/io5";
import { FaChevronCircleDown, FaCalendar } from "react-icons/fa";
import ProjectTodo from "@/app/dhannu/components/ProjectTodo";

export default function Page({ params }) {
  const routeParams = use(params);
  const ID = routeParams.projectId;
  const [projectDetails, setProjectDetails] = useState([]);

  const [projectName, setProjectName] = useState("");
  const [status, setStatus] = useState("");
  const [selectStatus, setSelectStatus] = useState(false);

  const [assignee, setAssignee] = useState([]);
  const [selectAssigne, setSelectAssigne] = useState(false);

  const [priority, setPriority] = useState("");
  const [selectPriority, setSelectPriority] = useState(false);

  const [lastDate, setLastDate] = useState("");
  const [aboutProject, setAboutProject] = useState("");

  const handleProjectDetails = () => {
    if (!projectName.trim()) return;

    const newProject = {
      id: ID + "-" + new Date().getTime(),
      name: projectName,
      status: status || "Not completed",
      space: ID,
      assignee: assignee,
      priority: priority || "Low",
      dueDate: lastDate || null,
      aboutProject: aboutProject || "",
      tasks: [],
    };

    setProjectDetails((prev) => [...prev, newProject]);

    // Reset form
    setProjectName("");
    setStatus("");
    setAssignee([]);
    setPriority("");
    setLastDate("");
    setAboutProject("");
  };

  return projectDetails.length > 0 ? (
    <ProjectTodo
      projectDetails={projectDetails}
      setProjectDetails={setProjectDetails}
      ID={ID}
    />
  ) : (
    <div className="w-full h-full bg-zinc-950 text-white p-8 rounded-2xl flex flex-col gap-10">
      {/* Project Name */}
      <div>
        <input
          type="text"
          value={projectName}
          onChange={(e) => setProjectName(e.target.value)}
          placeholder="New Project"
          className="text-2xl font-semibold bg-transparent outline-none w-full placeholder-zinc-500 text-zinc-200 border-b border-zinc-800 pb-2 focus:border-purple-500 transition duration-300"
        />
      </div>

      {/* Options */}
      <div className="flex flex-wrap gap-5">
        {/* Status */}
        <div className="flex-1 min-w-[220px] relative">
          <div
            onClick={() => setSelectStatus(!selectStatus)}
            className="flex items-center justify-between p-4 bg-zinc-900 border border-zinc-800 rounded-lg hover:border-purple-500/60 transition cursor-pointer shadow-md hover:shadow-purple-500/20"
          >
            <div className="flex items-center gap-3">
              <TbCircleDashed size={20} className="text-purple-400" />
              <div>
                <p className="text-xs text-zinc-500 uppercase tracking-wider">
                  Status
                </p>
                <p className="font-medium text-zinc-300 capitalize">
                  {status || "Select status"}
                </p>
              </div>
            </div>
            <FaChevronCircleDown
              size={18}
              className={`text-purple-400 transition-transform duration-300 ${
                selectStatus ? "rotate-180" : ""
              }`}
            />
          </div>

          {selectStatus && (
            <div className="absolute mt-2 w-full bg-zinc-900 border border-zinc-800 rounded-lg shadow-lg overflow-hidden z-10">
              {["Not completed", "In Progress", "Completed"].map((cur) => (
                <div
                  key={cur}
                  onClick={() => {
                    setStatus(cur);
                    setSelectStatus(false);
                  }}
                  className={`p-2 px-3 text-sm hover:bg-purple-600/30 cursor-pointer transition ${
                    status === cur
                      ? "bg-purple-600/20 text-purple-400 font-semibold"
                      : "text-zinc-300"
                  }`}
                >
                  {cur}
                </div>
              ))}
            </div>
          )}
        </div>

        {/* Assignee */}
        <div className="flex-1 min-w-[220px] relative">
          <div
            onClick={() => setSelectAssigne(!selectAssigne)}
            className="flex items-center justify-between p-4 bg-zinc-900 border border-zinc-800 rounded-lg hover:border-purple-500/60 transition cursor-pointer shadow-md hover:shadow-purple-500/20"
          >
            <div className="flex items-center gap-3">
              <IoPeople size={17} className="text-purple-400" />
              <div>
                <p className="text-xs text-zinc-500 uppercase tracking-wider">
                  Assignee
                </p>
                <p className="font-medium text-zinc-300 flex gap-1">
                  {assignee.length > 0
                    ? assignee.map((cur) => (
                        <span
                          key={cur}
                          className="w-6 h-6 flex items-center justify-center text-xs font-semibold rounded-full bg-purple-600"
                        >
                          {cur.charAt(0).toUpperCase()}
                        </span>
                      ))
                    : "Select Assignee"}
                </p>
              </div>
            </div>
            <FaChevronCircleDown
              size={17}
              className={`text-purple-400 transition-transform duration-300 ${
                selectAssigne ? "rotate-180" : ""
              }`}
            />
          </div>

          {selectAssigne && (
            <div className="absolute mt-2 w-full bg-zinc-900 border border-zinc-800 rounded-lg shadow-lg overflow-hidden z-10">
              {["Dhannu Kumar", "Rohit Jha", "Ankit Sharma"].map((user) => (
                <div
                  key={user}
                  onClick={() => {
                    setAssignee((prev) =>
                      prev.includes(user) ? prev : [...prev, user]
                    );
                    setSelectAssigne(false);
                  }}
                  className="p-2 px-3 text-sm hover:bg-purple-600/30 cursor-pointer text-zinc-300 transition"
                >
                  {user}
                </div>
              ))}
            </div>
          )}
        </div>

        {/* Priority */}
        <div className="flex-1 min-w-[220px] relative">
          <div
            onClick={() => setSelectPriority(!selectPriority)}
            className="flex items-center justify-between p-4 bg-zinc-900 border border-zinc-800 rounded-lg hover:border-purple-500/60 transition cursor-pointer shadow-md hover:shadow-purple-500/20"
          >
            <div className="flex items-center gap-3">
              <FaChevronCircleDown size={17} className="text-purple-400" />
              <div>
                <p className="text-xs text-zinc-500 uppercase tracking-wider">
                  Priority
                </p>
                <p className="font-medium text-zinc-300">
                  {priority || "Select Priority"}
                </p>
              </div>
            </div>
          </div>

          {selectPriority && (
            <div className="absolute mt-2 w-full bg-zinc-900 border border-zinc-800 rounded-lg shadow-lg overflow-hidden z-10">
              {["Low", "Medium", "High"].map((p) => (
                <div
                  key={p}
                  onClick={() => {
                    setPriority(p);
                    setSelectPriority(false);
                  }}
                  className={`p-2 px-3 text-sm hover:bg-purple-600/30 cursor-pointer transition ${
                    priority === p
                      ? "bg-purple-600/20 text-purple-400 font-semibold"
                      : "text-zinc-300"
                  }`}
                >
                  {p}
                </div>
              ))}
            </div>
          )}
        </div>

        {/* Due Date */}
        <div className="flex flex-col justify-center p-4 bg-zinc-900 border border-zinc-800 rounded-lg min-w-[220px] shadow-md hover:border-purple-500/60 transition">
          <div className="flex items-center gap-3">
            <FaCalendar size={15} className="text-purple-400" />
            <div>
              <p className="text-xs text-zinc-500 uppercase tracking-wider">
                {lastDate || "End Date"}
              </p>
              <input
                type="date"
                value={lastDate}
                onChange={(e) => setLastDate(e.target.value)}
                className="bg-transparent text-zinc-300 text-sm mt-1 outline-none border-b border-zinc-800 focus:border-purple-500 transition duration-300"
              />
            </div>
          </div>
        </div>
      </div>

      {/* About Project */}
      <div className="flex flex-col gap-2">
        <h2 className="text-zinc-400 font-semibold">About Project</h2>
        <textarea
          rows="4"
          value={aboutProject}
          onChange={(e) => setAboutProject(e.target.value)}
          placeholder="Describe your project goals..."
          className="w-full bg-zinc-900 border border-zinc-800 rounded-md p-3 text-zinc-300 outline-none focus:border-purple-500 resize-none transition shadow-sm hover:shadow-purple-500/10 duration-300"
        />
      </div>

      {/* Create Button */}
      <div className="flex justify-end">
        <button
          onClick={handleProjectDetails}
          className="bg-purple-600 cursor-pointer hover:bg-purple-700 px-6 py-2 rounded-lg font-semibold shadow-md hover:shadow-lg transition-all duration-300"
        >
          Create Project
        </button>
      </div>
    </div>
  );
}
